import React from "react";
import "../framework/site.scss";

export interface RecruitmentSectionProps {
    showHeader: boolean
}

export default class RecruitmentContentSection extends React.PureComponent<RecruitmentSectionProps> {
  render() {
    return (
        <section className="content">
            {this.props.showHeader ? <h5>Recruitment</h5> : <></>}
            <p>
                IT professionals are in high demand. Finding right people is challenging.
            </p>
            <p>
                We listen to you. We know the people. We connect the dots.
            </p>
            <p>
                Having right people is critical to the business success &mdash; whether this is a new project,
                technology update, UI/UX refresh or business as usual.
            </p>
            <p>
                The people you need are unlikely to be found via regular job advertising or referrals.
            </p>
            <p>
                We use our professional networks to find right people for you and your project.
            </p>
            <p>
                Talk to us about qualifications and skills required in your teams and how we can help you find right people.
            </p>
        </section>
    );
  }
}